<template>
  <div class="container py-5">
    <NavTabs />
    <Spinner v-if="isLoading" />
    <template v-else>
      <h1 class="mt-5">美食達人</h1>

      <hr />
      <div class="row text-center;">
        <div v-for="user in topUsers" :key="user.id" class="col-3">
          <router-link :to="'/users/'+user.id">
            <!-- router-link :to="{name:'user',params:{id:user.id}}" -->
            <img :src="user.image" width="140px" height="140px" />
          </router-link>
          <h2>{{user.name}}</h2>
          <span class="badge badge-secondary">追蹤人數：{{user.FollowerCount}}</span>
          <p>
            <button
              type="button"
              v-if="user.isFollowed"
              @click.prevent.stop="deleteFollow(user.id)"
              class="btn btn-danger"
            >取消追蹤</button>

            <button
              type="button"
              v-else
              @click.prevent.stop="addFollow(user.id)"
              class="btn btn-primary"
            >追蹤</button>
          </p>
        </div>
      </div>
    </template>
  </div>
</template>

<script>
import NavTabs from "../components/NavTabs";
import usersAPI from "../apis/users";
import { Toast } from "../utils/helpers";
import Spinner from "../components/Spinner";

export default {
  components: { NavTabs, Spinner },
  data() {
    return {
      topUsers: [],
      isLoading: true
    };
  },
  methods: {
    async fetchUsers() {
      try {
        const { data } = await usersAPI.getTopUsers();

        this.topUsers = data.users;
        this.isLoading = false;
      } catch (error) {
        this.isLoading = false;
        Toast.fire({
          icon: "error",
          title: "無法取得使用者資料，請稍後再試"
        });
      }
    },
    async addFollow(userId) {
      try {
        const { data } = await usersAPI.addFollow({ userId });
        if (data.status !== "success") {
          throw new Error(data.message);
        }
        this.topUsers.map(element => {
          if (element.id === userId) {
            element.isFollowed = true;
            element.FollowerCount++;
          }
        });
      } catch (error) {
        Toast.fire({
          icon: "error",
          title: "無法追蹤使用者，請稍後再試"
        });
      }
    },
    async deleteFollow(userId) {
      try {
        const { data } = await usersAPI.deleteFollow({ userId });
        if (data.status !== "success") {
          throw new Error(data.message);
        }
        this.topUsers.map(element => {
          if (element.id === userId) {
            element.isFollowed = false;
            element.FollowerCount--;
          }
        });
      } catch (error) {
        Toast.fire({
          icon: "error",
          title: "無法取消追蹤使用者，請稍後再試"
        });
      }
    }
  },
  created() {
    this.fetchUsers();
  }
};
</script>
